/**
 * bpm 
 */
import axios from 'axios';
import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { Button, Modal, Table ,Row,Label,Checkbox } from 'tinper-bee';
import RefTreeTransferWithInput from 'pap-refer/lib/pap-common-treeTransfer/src/index.js';
// import 'pap-refer/lib/pap-common-treeTransfer/src/index.css';
// import 'tinper-bee/assets/tinper-bee.css'
// import 'ref-tree-transfer/lib/index.less';
// import './tinper-bee-theme.css'
// import 'pap-refer/dist/index.css'
import './index.css'
// import './test.css'
import { onCommit, queryBpmTemplateAllocate, reconvert } from './common';
import refOptions from './refOptions';

const propTypes = {
    checkedArray: PropTypes.array,
    funccode: PropTypes.string,
    nodekey: PropTypes.string,
    url: PropTypes.string,
    urlAssignSubmit: PropTypes.string,
    className: PropTypes.string,
    filterRefUrl: PropTypes.string,
    refCode: PropTypes.string,
    size: PropTypes.string,
    scrollY: PropTypes.number,
    isOne: PropTypes.bool,
    onSuccess: PropTypes.func,
    onError: PropTypes.func,
    onStart: PropTypes.func,
    onEnd: PropTypes.func
};

class BpmButtonSubmit extends Component {
    constructor() {
        super();
        this.state = {
            childRefKey: [],//
            userIds: [],
            processDefineCode: "",
            assignInfo: {
                assignInfoItems: []
            },
            obj: [],//
            huanjieShow: false,//
            huanjieList: [],
            chaosongShow:false,//
            editRowIndex: 0,
            showVal: [],
            checkedArray:[], //
            copyusers:[],   //
            intersection:true,  //
            submiting:false
        }
    }
    //
    handlerBtn = async () => {
        let { checkedArray, isOne, onStart, onEnd, onSuccess, onError } = this.props;
        if(this.props.children.props.disabled){
            return
        }
        //
        if (isOne && checkedArray.length >= 2) {
            onError && onError({
                type: 2,
                msg: ``
            });
            return;
        }

        //
        if (checkedArray.length > 0) {
            //bpmState==null or 0 
            if (checkedArray[0].bpmState >= 1) {
                onError && onError({
                    type: 1,
                    msg: ``
                });
                return;
            }
            //
            onStart && onStart();
            //res_codefunccode,nodekey
            let { data: { success, detailMsg } } = await queryBpmTemplateAllocate({
                funccode: this.props.funccode,
                nodekey: this.props.nodekey
            });
            if(!detailMsg.data ||detailMsg.data==null){
                onError && onError({
                    type: 1,
                    msg: ``
                });
                return;
            }
            //
            if (success == "success") {
                //
                let commitParam = {
                    "url": this.props.url,
                    "processDefineCode": detailMsg.data.res_code,
                    "submitArray": checkedArray
                }
                //res_code
                this.setState({
                    processDefineCode: detailMsg.data.res_code
                });
                //submit
                let result = await onCommit(commitParam);
                let flag = result.data.success;
                //
                if (flag == "success" && (!result.data.detailMsg.data.assignAble)) {
                    //
                    onSuccess && onSuccess();
                } else if (flag == "fail_global") {
                    //
                    onError && onError({
                        type: 2,
                        msg: reconvert(result.data.message) || ''
                    });
                }
                //
                if (result.data.detailMsg.data && result.data.detailMsg.data.assignAble == true) {
                    //idname
                    if (result.data.detailMsg.data.assignedActivities && result.data.detailMsg.data.assignedActivities.length > 0) {
                        //
                        onEnd && onEnd();
                        let arr = result.data.detailMsg.data.assignedActivities.filter( (item)=>{ return !item.properties.startactivity;});
                        //
                        this.setState({
                            huanjieShow: true,
                            chaosongShow:result.data.detailMsg.data.assignedActivities[0].properties.iscopytouser,
                            huanjieList: arr,
                            obj: checkedArray,
                            assignInfo: {
                                assignInfoItems: Array.from(arr, x => ({ activityId: x.id, activityName: x.name, participants: [] }))
                            }
                        });
                    }
                }
            } else if (success == "fail_global") {
                let { data: { message } } = result
                //
                onError && onError({
                    type: 2,
                    msg: reconvert(message) || ''
                });
            }
        } else {
            // 
            onError && onError({
                type: 1,
                msg: ``
            });
        }

    }
    //
    closeHuanjie = () => {
        this.setState({
            huanjieShow: false,
            chaosongShow:false,
            childRefKey: [],
            showVal: []
        });
    }
    //
    signAddOK = () => {
        //
        let _index = this.state.editRowIndex;
        //
        let sourseArray = this.state.assignInfo.assignInfoItems.slice();
        //
        sourseArray[_index]['participants'] = Array.from(this.state.userIds, x => ({ id: x.id }));
        this.setState({
            assignInfo: {
                assignInfoItems: sourseArray
            },
            userIds: []
        });
    }
    //
    huanjieHandlerOK = async () => {
        let { urlAssignSubmit, onSuccess, onError, onStart, onEnd } = this.props;
        let { processDefineCode, assignInfo, obj,copyusers,intersection,submiting } = this.state;
        obj=obj[0];
        let arr=[];
        let self = this;
        copyusers.map(function(value) {
            arr=arr.concat(value);
        });
        copyusers=arr;
        //
        if(!submiting){
            onStart && onStart();
            this.setState({
                submiting:true
            })
            let result = await axios.post(urlAssignSubmit, {
                processDefineCode,
                assignInfo,
                obj,
                copyusers,
                intersection

            }).catch((e) => {

                onError && onError({
                    type: 2,
                    msg: ``
                });
                self.setState({
                    submiting:false
                })
            });
            if (result.data.success == 'success') {
                onSuccess && onSuccess();
                this.setState({
                    huanjieShow: false,
                    chaosongShow:false,
                    childRefKey: [],
                    showVal: [],
                    submiting:false
                });
            } else if (result.data.success == 'fail_global') {
                onError && onError({
                    type: 2,
                    msg: reconvert(result.data.message) || ''
                });
                this.setState({
                    huanjieShow: false,
                    chaosongShow:false,
                    childRefKey: [],
                    showVal: [],
                    submiting:false
                });
            }
        }
    }
    changeCheck=()=> {
        this.setState({intersection:!this.state.intersection});
    }
    render() {
        let self = this;
        let huanjieCol = [{
            title: "",
            dataIndex: "name",
            key: "name",
            width: "40%"
        },
        {
            title: "",
            dataIndex: "id",
            key: "id",
            width: "40%"
        }, {
            title: "",
            dataIndex: "1",
            key: "1",
            width: "20%",
            render(text, record, index) {

                const option = {
                    title: '',
                    textOption: {
                        modalTitle: '',
                        leftTitle: '',
                        rightTitle: '',
                        leftTransferText: '',
                        rightTransferText: '',

                    },
                    param: {//url
                        refCode: 'neworgdeptuser_treegrid',//test_common||test_grid||test_tree||test_treeTable
                        // refModelUrl: 'http://workbench.yyuap.com/ref/rest/testref_ctr/',
                    },
                    refModelUrl: {
                        treeUrl: '/pap_basedoc/common-ref/blobRefTree',
                        tableBodyUrlSearch: '',
                        tableBodyUrl:'/pap_basedoc/common-ref/blobRefTreeGrid',//
                        refInfo:'/pap_basedoc/common-ref/refInfo',//
                    },
                    jsonp: false,
                    hearders: {},
                    displayField: '{refname}',//
                    valueField: 'refpk',// value 
                    onCancel: function (p) {
                        console.log(p)
                    },
                    onSave: function (sels,showVal) {
                        console.log(sels);
                        var temp = sels.map(v => v.id);
                        //
                        let _showVal = self.state.showVal.slice();
                        _showVal[index] = showVal;
                        //
                        let _childRefKey = self.state.childRefKey.slice();
                        _childRefKey[index] = temp;
                        //
                        let sourseArray = self.state.assignInfo.assignInfoItems.slice();
                        //
                        sourseArray[index]['participants'] = Array.from(_childRefKey[index], x => ({ id: x }));
                        let checkedArray = self.state.checkedArray;
                        checkedArray[index] = sels;
                        self.setState({
                            checkedArray:checkedArray,
                            childRefKey: _childRefKey,
                            showVal: _showVal,
                            assignInfo: {
                                assignInfoItems: sourseArray
                            }
                        });
                    }
                }

                return <RefTreeTransferWithInput {...option}/>
            }
        }]


        return (<span>
            <span onClick={this.handlerBtn}>
                {
                    this.props.children
                }
            </span>
            <Modal
                size={this.props.size}
                show={this.state.huanjieShow||this.state.chaosongShow}
                backdrop={false}
                enforceFocus={false}
                onHide={this.closeHuanjie}>
                <Modal.Header closeButton>
                    <Modal.Title> {''}</Modal.Title>
                </Modal.Header>
                {this.state.huanjieShow?<Modal.Body>
                    <Table
                        rowKey={record => record.id}
                        columns={huanjieCol}
                        data={this.state.huanjieList}
                        scroll={{ x: "100%", y: 200 }}
                    />
                </Modal.Body>:""}
                <Modal.Footer>
                    <Button style={{ "marginRight": "10px" }}  onClick={this.closeHuanjie}>  </Button>
                    <Button colors="primary"  onClick={this.huanjieHandlerOK}>  </Button>

                </Modal.Footer>
            </Modal>
        </span>);
    }
}
BpmButtonSubmit.propTypes = propTypes;
BpmButtonSubmit.defaultProps = {
    checkedArray: [],
    nodekey: "003",
    funccode: "react",
    url: "/example/ygdemo_yw_info/submit",
    urlAssignSubmit: "/example/ygdemo_yw_info/assignSubmit",
    className: "",
    filterRefUrl: "/iuap_pap_quickstart/common/filterRef",
    refCode: "newuser",
    size: "",
    scrollY: 270,
    isOne: false,
    organrefCode:"newdept",
    positonrefCode:"newposition",
    roleRef:"newRoleRef",
    userRef:"newuser"
}
export default BpmButtonSubmit;
